<template>
  <div class="home">
  <div v-if="sucess_connect" class="message-sucess">
    <p>Conectado com sucesso !!</p>
  </div>
  <div v-if="error" class="message-error">
    <p>{{error}}</p>
    <p @click="error = ''" style="cursor:pointer;">X</p>
  </div>
   <div class="chat">
     <img src="../assets/logo2.png" alt="" srcset="">
     <div class="box-chat">
       <div v-if="loading" class="loading">
         <p>Carregando o chat ....</p>
       </div>
       <div  v-for="message in chat" :key="message.key" class="message">
         <p>{{message.name}}:</p>
         <p>{{message.content}}</p>
       </div>
     </div>
     <div class="form">
       <input v-model="msg" type="text" placeholder="Type your message" name="" id="">
       <div @click="submit" class="btn-enviar">
         <i class="material-icons">send</i>
       </div>
     </div>
   </div>
  </div>
</template>

<script>
import firebase from 'firebase'
// @ is an alias to /src
export default {
  name: 'Home',
  data () {
    return {
      chat: [],
      msg: '',
      nome: 'Guilherme',
      loading: 'true',
      error: '',
      sucess_connect: 'true'
    }
  },
  created () {
    firebase.database().ref('chat').on('value', (snapshot) => {
      this.chat = snapshot.val()
      this.loading = false
    })
    setTimeout(() => {
      this.sucess_connect = ''
    }, 1000)
  },
  methods: {
    submit () {
      if (!this.msg) {
        this.error = 'Preencha o campo mensagem'
      } else {
        firebase.database().ref('chat').push({ name: this.nome, content: this.msg })
      }
    }
  }
}
</script>

